:root {
  --accent: #3b82f6;
  --text-primary: #1f2937;
  --text-secondary: #6b7280;
  --text-muted: #9ca3af;
  --border-light: #e5e7eb;
  --border-focus: var(--accent);
  --bg-white: #ffffff;
  --bg-gray-50: #f9fafb;
  --bg-gray-100: #f3f4f6;
  --shadow-md: 0 4px 6px rgba(0,0,0,0.11);
  --radius-md: 6px;
  --radius-lg: 12px;
  --spacing-md: 1rem;
  --spacing-lg: 2rem;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Arial, sans-serif;
  background: var(--bg-gray-50);
  color: var(--text-primary);
  margin: 0;
  padding: 0;
}

.crm-navbar {
  background: var(--bg-white);
  box-shadow: var(--shadow-md);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem var(--spacing-lg);
  gap: 2rem;
}
.crm-logo {
  text-decoration: none;
  color: var(--accent);
  font-size: 1.25rem;
  font-weight: 700;
  letter-spacing: 0.01em;
}
.crm-navbar nav a {
  text-decoration: none;
  color: var(--text-primary);
  margin-left: 2rem;
  font-size: 1rem;
  font-weight: 500;
  padding: 0.5rem 0.75rem;
  border-radius: var(--radius-md);
  transition: background 0.18s;
}
.crm-navbar nav a.active,
.crm-navbar nav a[style*="font-weight: 600"] {
  background: var(--accent);
  color: #fff;
}

.crm-main {
  padding: var(--spacing-lg) 0;
  max-width: 900px;
  margin: 0 auto;
}

.form-container {
  background: var(--bg-white);
  box-shadow: var(--shadow-md);
  border-radius: var(--radius-lg);
  padding: var(--spacing-lg);
  max-width: 540px;
  margin: 1.5rem auto;
}
input, select, textarea {
  width: 100%;
  margin-bottom: 0.2rem;
  padding: 0.4rem;
  font-size: 1rem;
  border: 1px solid var(--border-light);
  border-radius: var(--radius-md);
  box-sizing: border-box;
  outline: none;
  transition: border 0.2s, box-shadow 0.2s;
}
input:focus, select:focus, textarea:focus {
  border-color: var(--accent);
  box-shadow: 0 0 0 3px rgba(59,130,246,0.1);
}
label {
  display: block;
  font-size: 0.97rem;
  font-weight: 500;
  color: var(--text-secondary);
  margin-bottom: 0.2rem;
}
.btn-primary {
  background: var(--accent);
  color: #fff;
  border: 1px solid var(--accent);
  border-radius: var(--radius-md);
  padding: 0.4rem 1.1rem;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.18s, color 0.18s;
}
.btn-primary:hover {
  background: #2462bb;
}
.btn-secondary {
  background: transparent;
  border: 1px solid var(--accent);
  color: var(--accent);
  border-radius: var(--radius-md);
  padding: 0.4rem 1.1rem;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.18s, color 0.18s;
}
.btn-secondary:hover {
  background: var(--accent);
  color: #fff;
}
.error {
  color: #dc2626;
  border-color: #ef4444;
  font-size: 0.98rem;
  margin: 0.2rem 0 0.2rem 0;
}
.empty-state {
  text-align: center;
  color: var(--text-muted);
  font-style: italic;
  padding: 2rem 0.8rem;
}

.table-container {
  background: var(--bg-white);
  box-shadow: var(--shadow-md);
  border-radius: var(--radius-lg);
  padding: 2rem;
  margin-bottom: 2.5rem;
}
.customer-table, .interaction-table {
  width: 100%;
  margin-top: 1.2rem;
  border-collapse: collapse;
  border: 1px solid var(--border-light);
  background: var(--bg-white);
}
.customer-table th, .customer-table td,
.interaction-table th, .interaction-table td {
  border: 1px solid var(--border-light);
  padding: 0.6rem 0.5rem;
  text-align: left;
  vertical-align: top;
}
.customer-table th, .interaction-table th {
  background: var(--bg-gray-100);
}
.customer-table td:last-child, .interaction-table td:last-child {
  white-space: nowrap;
}
.detail-container {
  background: var(--bg-white);
  box-shadow: var(--shadow-md);
  border-radius: var(--radius-lg);
  padding: 2.3rem 2rem;
  margin-bottom: 2rem;
}
.detail-fields > div {
  margin-bottom: 0.6rem;
}
@media (max-width: 650px) {
  .crm-navbar { flex-direction: column; padding: 0.6rem 0.5rem; }
  .crm-main { padding: 1rem 0.3rem; }
  .form-container, .table-container, .detail-container { padding: 1rem; }
  .customer-table th, .customer-table td, .interaction-table th, .interaction-table td { font-size: 0.95rem; }
}
@media (max-width: 400px) {
  .form-container, .table-container, .detail-container {
    padding: 0.6rem 0.15rem;
  }
  input, select, textarea {
    font-size: 0.93rem;
  }
}
